<template>
  <div class="hello">
    <div>
      <h2>Use Input</h2>
      <p>input: <input
          type="text"
          v-model="inputText"
        ></p>
      <p>text: {{ inputText }}</p>
    </div>

    <div>
      <h2>Counter</h2>
      {{count}}
      <button @click="increment">+</button>
      <button @click="decrement">-</button>
    </div>

    <div>
      <h2>Show/Hide</h2>
      <label for="checkbox">Show/Hide</label>
      <input
        type="checkbox"
        id="checkbox"
        v-model="seen"
      >
      <p v-if="seen">Now you see me</p>
    </div>

    <div>
      <h2>Use select</h2>
      <select v-model="selected">
        <option
          disabled
          value='None'
        >Please select one</option>
        <option>A</option>
        <option>B</option>
        <option>C</option>
      </select>
      <p>Selected: {{ selected }}</p>
    </div>

    <div>
      <h2>Use v-for</h2>
      <ul id="example-1">
        <li
          v-for="item in itemList"
          v-bind:key="item.key"
        >
          {{ item.key }} : {{item.value}}
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      inputText: "init text",
      count: 0,
      seen: false,
      selected: "",
      itemList: [
        { key: "a", value: 0 },
        { key: "b", value: 1 },
        { key: "c", value: 2 },
        { key: "d", value: 3 }
      ]
    };
  },
  methods: {
    increment() {
      this.count = this.count + 1;
    },
    decrement() {
      this.count = this.count - 1;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



